@font-face {
  font-family: "montserrat-extralight";
  src: local("montserrat-extralight"),
    url(../images/fonts/Montserrat/Montserrat-ExtraLight.ttf) format("truetype");
}
@font-face {
  font-family: "montserrat-light";
  src: local("montserrat-light"),
    url(../images/fonts/Montserrat/Montserrat-Light.ttf) format("truetype");
}
@font-face {
  font-family: "montserrat-semibold";
  src: local("montserrat-semibold"),
    url(../images/fonts/Montserrat/Montserrat-SemiBold.ttf) format("truetype");
}
@font-face {
  font-family: "galano-alt";
  src: local("galano-alt"),
    url(../images/fonts/GalanoGrotesque/GalanoGrotesqueAltDEMO-Bold.ttf)
      format("truetype");
}

/* video {
