  z-index: -1;
} */

.image-container {
  position: absolute;
  width: 60%;
  height: 100vh;
  top: 0;
  right: 0;
  overflow: hidden;
}

.image-container img {
  width: 847px;
  z-index: -1;
  transform: scale(1.77);
  transform-origin: 0% -10%;
}

.hero-container {
  position: absolute;
  top: 0;
  background-color: #191919;
  height: 100vh;
  width: 100%;
  object-fit: contain;
}
.content-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  object-fit: contain;
  margin: 360px 0 0 5vw;
  width: 60%;
}

.content-container > h1 {
  color: #ffffff;
  font-size: 45px;
  margin-top: -100px;
  font-family: "galano-alt", Geneva, Tahoma, sans-serif;
}

.content-container > p {
  display: inline;
  margin-top: 25px;
  color: #ffffff;
  font-size: 20px;
  width: 70%;
  font-family: "montserrat-extralight", "Trebuchet MS", "Lucida Sans Unicode";
}

.typing-texts {
  display: inline;
}
.typing-texts span {
  color: #ff3502;
  font-family: "montserrat-light", "Trebuchet MS", "Lucida Sans Unicode";
}

.Typist .Cursor {
  display: inline-block;
  color: #ffffff;
}
.Typist .Cursor--blinking {
  opacity: 1;
  animation: blink 1s linear infinite;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.hero-btns {
  margin-top: 70px;
}

.hero-btns .btn {
  font-family: "montserrat-semibold", "Trebuchet MS";
  color: #ffffff;
  margin: 7px 40px 0 0;
  min-width: 300px;
}

.footer-landing {
  position: absolute;
  z-index: 1;
  left: 5vw;
  bottom: 20px;
}

.footer-landing p {
  color: #ffffff;
  font-family: "montserrat-extralight", sans-serif;
  font-size: 0.8rem;
}
@media screen and (max-width: 1400px) {
  .content-container {
    margin: 300px 0 0 5vw;
  }

  .content-container > h1 {
    font-size: 33px;
  }
  .content-container > p {
    font-size: 15px;
  }
  .hero-btns .btn {
    margin: 7px 40px 0 0;
    min-width: 225px;
    font-size: 15px;
  }
  .image-container img {
    width: 450px;
    transform-origin: -50% -22%;
  }
  .footer-landing p {
    font-size: 0.5rem;
  }
}

@media screen and (max-width: 960px) {
  .content-container > h1 {
    font-size: 20px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 760px) {
  .content-container {
    margin-top: 250px;
    width: 80%;
  }
  .content-container > h1 {
    font-size: 20px;
    margin-top: -100px;
    width: 100%;
  }
  .content-container > p {
    font-size: 10px;
    width: 100%;
  }
  .btn-mobile {
    display: block;
    text-decoration: none;
  }
  .btn {
    width: 100%;
  }
  .image-container img {
    width: 430px;
  }
}
