.contact-us_container label {
  font-family: "montserrat-semibold", "Trebuchet MS", "Lucida Sans Unicode";
}

.contact-us_container input {
  /* May help keep right column responsive
	width: cal(100%-200px); */
  height: 3em;
  border: solid 3px #191919;
  padding: 0 1em;
  background-color: #2d2d2d;
}

.contact-us_container input,
textarea {
  font-family: "montserrat-extralight", "Trebuchet MS", "Lucida Sans Unicode";
  color: #ffffff;
  font-size: 18px;
  display: block;
  width: 100%;
}

input[type="email"]:invalid:not(:focus):not(:placeholder-shown) {
  background: rgba(255, 0, 0, 0.041);
}

.contact-us_container textarea {
  border: solid 3px #191919;
  padding: 0.5em 1em;
  background-color: #2d2d2d;
}

.floating-form {
  width: 320px;
}

/****  floating-Lable style start ****/
.floating-label {
  position: relative;
  margin-bottom: 20px;
}
/* .floating-input,
.floating-select {
  font-size: 14px;
  padding: 4px 4px;
  display: block;
  width: 100%;
  height: 30px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #757575;
} */

.floating-input:focus,
.floating-select:focus {
  outline: none;
  border-bottom: 2px solid #ff3502;
}

label {
  color: #999;
  font-size: 20px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 10px;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.floating-input:focus ~ label,
.floating-input:not(:placeholder-shown) ~ label {
  top: -18px;
  font-size: 14px;
  color: #ff3502;
}

.floating-select:focus ~ label,
.floating-select:not([value=""]):valid ~ label {
  top: -18px;
  font-size: 14px;
  color: #ff3502;
}

/* active state */
.floating-input:focus ~ .bar:before,
.floating-input:focus ~ .bar:after,
.floating-select:focus ~ .bar:before,
.floating-select:focus ~ .bar:after {
  width: 50%;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.floating-textarea {
  min-height: 30px;
  max-height: 260px;
  overflow: hidden;
  overflow-x: hidden;
}

/* highlighter */
.highlight {
  position: absolute;
  height: 50%;
  width: 100%;
  top: 15%;
  left: 0;
  pointer-events: none;
  opacity: 0.5;
}

/* active state */
.floating-input:focus ~ .highlight,
.floating-select:focus ~ .highlight {
  -webkit-animation: inputHighlighter 0.3s ease;
  -moz-animation: inputHighlighter 0.3s ease;
  animation: inputHighlighter 0.3s ease;
}

/* animation */
@-webkit-keyframes inputHighlighter {
  from {
    background: #5264ae;
  }
  to {
    width: 0;
    background: transparent;
  }
}
@-moz-keyframes inputHighlighter {
  from {
    background: #5264ae;
  }
  to {
    width: 0;
    background: transparent;
  }
}
@keyframes inputHighlighter {
  from {
    background: #5264ae;
  }
  to {
    width: 0;
    background: transparent;
  }
}

/****  floating-Lable style end ****/

.contact-us_container input:focus,
textarea:focus {
  background: #111111;
  outline: none;
}

.contact-us_container .button-container {
  grid-column: 1/4;
}

.contact-us_container .button {
  width: 5.5em;
  background: green;
  color: white;
  border: none;
  cursor: pointer;
}

.contact-us_container .button:hover {
  opacity: 0.7;
}

@media screen and (max-width: 1024px) {
  /* Specific to background image */
  img #background-img {
    left: 50%;
    margin-left: -512px; /* 50% */
  }
}

/* For Mobile */
@media (max-width: 750px) {
  .contact-us_container {
    height: auto;
  }
  .contact-us_container .container {
    width: 90%;
  }

  .contact-us_container form {
    display: flex;
    flex-direction: column;
  }

  .name {
    grid-column-gap: 0;
  }
  p#break {
    display: inline;
  }
}
