:root {
  --primary: #2293e9;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background-color: var(--primary);
  counter-reset: #242424;
  border-radius: 4px;
}

.btn--outline {
  background-color: transparent;
  color: #ffffff;
  padding: 8px 20px;
  border: 1px solid var(--primary);
  border-radius: 4px;
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 4px 20px;
  font-size: 20px;
}
.btn--large {
  padding: 8px 26px;
  font-size: 28px;
}
@media screen and (min-width: 760px) {
  .btn--large {
    min-width: 280px;
  }
}
.btn--medium:hover,
.btn--large:hover {
  background: #ff3502;
  color: #ffffff;
  transition: 250ms;
}
